import { Button } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import { makeStyles, createStyles, Theme} from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import * as React from 'react';
import { UrlUtils } from '../utils/url-utils';


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexDirection: 'row',
      '& .MuiTextField-root': {
        margin: theme.spacing(1),
        width: '25ch',
      },
    },
    button: {
      marginLeft: theme.spacing(1),
      marginTop: 'auto',
      marginBottom: 'auto'
    }
  }),
);

interface Props {
  addVideo: (url: string) => void;
}

function useUrl(): [ string, React.Dispatch<React.SetStateAction<string>>, React.ChangeEventHandler<HTMLInputElement>] {
  const [ url, setUrl ] = React.useState('');
  return [
    url, 
    setUrl,
    e => setUrl(e.target.value),
  ]
}

export function VideoInput({addVideo}: Props): JSX.Element {
  const classes = useStyles();
  const [url, setUrl, setUrlEventHandler] = useUrl(); 
  const addVideoEventHandler = () => {
    addVideo(url);
    setUrl('');
  }

  return (
    <Paper elevation={3} className={classes.root}>
      <TextField id="outlined-basic" label="URL" variant="outlined" value={url} onChange={setUrlEventHandler} />
      <Button className={classes.button} disabled={!UrlUtils.getVideoIdIfUrlValid(url)} onClick={addVideoEventHandler}>Add</Button>
    </Paper>
  );
}