import Paper from '@material-ui/core/Paper';
import * as React from 'react';
import YouTube, { Options } from 'react-youtube';
import { connect } from 'react-redux'
import autobind from 'autobind-decorator';
import { AnyAction, Dispatch } from 'redux';

import { VideoState } from '../store/reducer';
import { VideoActions } from '../store';

interface StateProps{
  videoIds: string[];
}

interface DispatchProps{
  removeVideo: (id: string) => void;
  videoWatched: (id: string) => void;
}

interface Props extends StateProps, DispatchProps {

}

interface State {
  videoIndex: number;
}

const opts: Options = {
  height: '390',
  width: '640',
  playerVars: {
    // https://developers.google.com/youtube/player_parameters
    autoplay: 1,
  },
};

class Component extends React.PureComponent<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {videoIndex: 0};
  }

  public static getDerivedStateFromProps(props: Props, state: State): Partial<State> {
    if (state.videoIndex + 1 > props.videoIds.length) {
      return {videoIndex: 0};
    }
    return null;
  }

  public render(): React.ReactNode {
    return (
      <>
        <Paper elevation={3}>
          <YouTube
            opts={opts}
            videoId={this.props.videoIds[this.state.videoIndex]} 
            onReady={() => console.log('ready')}
            onEnd={this.onEnd}
            onError={this.onError}
          />
        </Paper>
      </>
    );
  }

  @autobind
  private onEnd(): void {
    this.setState(
      {videoIndex: this.state.videoIndex + 1}, 
      () => this.props.videoWatched(this.props.videoIds[this.state.videoIndex]),
    );
  }

  @autobind
  private onError(): void {
    this.props.removeVideo(this.props.videoIds[this.state.videoIndex]);
  }
}

function mapStateToProps(state: VideoState): StateProps {
  return {
    videoIds: state.videoList,
  }
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchProps {
  return {
    removeVideo: (id) => dispatch(VideoActions.removeVideo(id)), 
    videoWatched: (id) => dispatch(VideoActions.videoWatched(id)),
  }
}

export const VideoRunner = connect(mapStateToProps, mapDispatchToProps)(Component);