import { AnyAction } from "redux";
import { MonoliteHelper } from "../utils/monolite-helper";
import { ReducerMethods, Video } from "../interfaces";
import { VideoActionTypes } from "./action-types";

export type VideoState = {
  videoList: string[];
  videoData: Record<string, Video>;
}

const reducerMethods: ReducerMethods<VideoState> = {
  [VideoActionTypes.ADD_VIDEO]: (state, payload: Video) => {
    return new MonoliteHelper(state)
      .setConcat(x => x.videoList, [payload.id])
      .set(x => x.videoData[payload.id], x => x || payload)
      .get();
  },
  [VideoActionTypes.REMOVE_VIDEO]: (state, payload: string) => {
    return new MonoliteHelper(state)
      .setFilter(x => x.videoList, x => x !== payload)
      .get();
  },
  [VideoActionTypes.VIDEO_WATCHED]: (state, payload: string) => {
    return new MonoliteHelper(state)
      .set(_ => _.videoData[payload].timesWatched, x => x + 1)
      .get();
  },
  [VideoActionTypes.REMOVE_FROM_LIST]: (state, payload: number) => {
    return new MonoliteHelper(state)
      .removeKey(_ => _.videoList, payload)
      .get();
  }
}

export function videos(state: VideoState = {videoList: [], videoData: {}}, action: AnyAction): VideoState {
  if(action && action.type in reducerMethods) {
    return reducerMethods[action.type](state, action.payload);
  } else {
    return state;
  }
}