import Paper from '@material-ui/core/Paper';
import * as React from 'react';
import { connect } from 'react-redux';
import makeStyles from '@material-ui/core/styles/makeStyles';
import createStyles from '@material-ui/core/styles/createStyles';
import { IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { AnyAction, Dispatch } from 'redux';

import { Video } from '../interfaces';
import { VideoState } from '../store/reducer';
import { VideoActions } from '../store';




interface StateProps{
  video: Video;
}

interface DispatchProps{
  onRemove: () => void;
}

interface OwnProps{
  videoId: string;
  index: number;
}

interface Props extends StateProps, OwnProps, DispatchProps{

}

const useStyles = makeStyles(() =>
  createStyles({
    image: {
      height: '100px',
    },
    root: {
      padding: '10px',
      display: 'flex',
      flexDirection: 'row',
    },
    timesWatched: {
      marginTop: 'auto',
      marginBottom: 'auto',
      fontSize: `35px`,
      marginLeft: `15px`,
    },
  }),
);


export function Component({video, onRemove}: Props): JSX.Element {
  const style = useStyles();
  return (
    <Paper elevation={3} className={style.root}>
      <img className={style.image} src={video.thumbnailUrl}/>
      <div className={style.timesWatched}>{video.timesWatched}</div>
      <IconButton onClick={onRemove}>
        <DeleteIcon/>
      </IconButton>
    </Paper>
  )
}

function mapStateToProps(state: VideoState, ownProps: OwnProps): StateProps{
  return {
    video: state.videoData[ownProps.videoId],
  }
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>, {index}: OwnProps): DispatchProps {
  return {
    onRemove: () => dispatch(VideoActions.removeVideoFromList(index)),
  }
}

export const VideoView =  connect(mapStateToProps, mapDispatchToProps)(Component);
