import * as monolite from 'monolite';

export class MonoliteHelper<T> {
  private state: T;
  constructor(state: T) {
    this.state = state;
  }

  public set<U>(
    accessor: (sourceState: T) => U,
    value: U | ((_: U) => U),
  ): MonoliteHelper<T> {
    this.state = monolite.set(this.state, accessor, value);
    return this;
  }

  public removeKey<U>(
    accessor: (sourceState: T) => U,
    key: string | number,
  ): MonoliteHelper<T>{
    this.state = monolite.set(this.state, accessor, (obj) => {
      const newValue = { ...obj };
      delete newValue[key];
      return newValue;
    });
    return this;
  }

  public setFromStringArr<U>(
    path: string[],
    value: U,
  ): MonoliteHelper<T> {
    this.state = monolite.set(this.state, path, value);
    return this;
  }

  public setConcat<U>(
    accessor: (sourceState: T) => U[],
    value: U[],
  ): MonoliteHelper<T> {
    return this.set(accessor, _ => _.concat(value));
  }

  public setSort <U>(
    accessor: (sourceState: T) => U[],
    comparer: (item1: U, item2: U) => number,
  ): MonoliteHelper<T> {
    this.state = monolite.set(this.state, accessor, (arr) => {
      return arr.sort(comparer);
    });
    return this;
  }

  public setFilter<U>(
    accessor: (sourceState: T) => U[],
    filter: (item: U) => boolean,
  ): MonoliteHelper<T>{
    this.state = monolite.set(this.state, accessor, item =>  item.filter(filter));
    return this;
  }

  public get(): T {
    return this.state;
  }
}
