import { ActionWith, Video } from "../interfaces";
import { VideoActionTypes } from "./action-types";

function addVideo(video: Video): ActionWith<Video> {
  return {
    type: VideoActionTypes.ADD_VIDEO,
    payload: video,
  }
}

function removeVideo(id: string): ActionWith<string> {
  return {
    type: VideoActionTypes.REMOVE_VIDEO,
    payload: id,
  };
}

function videoWatched(id: string): ActionWith<string> {
  return {
    type: VideoActionTypes.VIDEO_WATCHED,
    payload: id,
  }
}

function removeVideoFromList(index: number): ActionWith<number> {
  return {
    type: VideoActionTypes.REMOVE_FROM_LIST,
    payload: index,
  }
}

export const VideoActions = {
  addVideo,
  removeVideo,
  videoWatched,
  removeVideoFromList,
};