import * as React from 'react';
import { AnyAction, Dispatch } from 'redux';
import { connect } from 'react-redux';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Typography from '@material-ui/core/Typography';
import AccordionDetails from '@material-ui/core/AccordionDetails';

import { Video } from '../interfaces';
import { VideoActions } from '../store';
import { VideoState } from '../store/reducer';
import { VideoInput } from './video-input';
import { UrlUtils } from '../utils/url-utils';
import { VideoView } from './video-view';
import { List } from '@material-ui/core';




interface DispatchProps{
  addVideo: (video: Video) => void;
}

interface StateProps{
  videos: string[];
}

interface Props extends DispatchProps, StateProps{
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    heading: {
      fontSize: theme.typography.pxToRem(15),
      flexBasis: '33.33%',
      flexShrink: 0,
    },
    secondaryHeading: {
      fontSize: theme.typography.pxToRem(15),
      color: theme.palette.text.secondary,
    },
  }),
);

function Component(props: Props){
  const  addVideo = (videoUrl: string) =>  {
    const videoData = UrlUtils.getVideoData(videoUrl);
    if (videoData) {
      props.addVideo(videoData);
    }
  }
  const [expanded, setExpanded] = React.useState<boolean>(false);
  const handleChange = () => {
    setExpanded(!expanded);
  };
  const classes = useStyles();
  return (
    <>
      <Accordion expanded={expanded} onChange={handleChange}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.heading}>Videos</Typography>
          <Typography className={classes.secondaryHeading}> Playlist length: {props.videos.length}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <List>
           {props.videos.map((x, i) => <VideoView videoId={x} key={`${x}-${i}`}/>)}
          </List>
        </AccordionDetails>
      </Accordion>
      <VideoInput addVideo={addVideo}/>
    </>
  )
}

function mapStateToProps(state: VideoState): StateProps {
  return {
    videos: state.videoList,
  }
}

function mapDispatchToProps(dispatch: Dispatch<AnyAction>): DispatchProps {
  return {
    addVideo: video => dispatch(VideoActions.addVideo(video)),
  };
}

export const VideoList = connect(mapStateToProps, mapDispatchToProps)(Component);